* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}


li {
  list-style: none;
}

h2 {
  margin: 0;
}

.main-list {
  display: flex;
  align-items: center;
}

.image {
  width: 30px;
  height: 20px;
  margin-right: 10px;
}

.country-desc {
  font-size: 16px;
}

.paragraph {
  margin-right: 5px;
  font-weight: 800;
  margin-bottom: 10px;
}


